// @flow
import _get from 'lodash/get';
import _pick from 'lodash/pick';
import _isEmpty from 'lodash/isEmpty';
import type { Offer as TOffer } from 'types/Offer';
import { FUNDING_PURCHASE_TYPES, CONTINGENCY_TYPES } from 'shared/Consts';
import {
  displayContingency,
  displayLoanType,
  displayFurniture,
  getBuyerName,
  getSellerName,
  getFullName,
} from '../../../helpers/Offer';
import { formatPrice } from 'helpers/number';
import { formatDate } from 'helpers/Datetime';
import type { TField, TCounter, TCountersGroup, TTableRow } from './types';

const COUNTER_TYPES = {
  original: 'original',
  buyer: 'buyer',
  agent: 'agent',
};
const DAYS_TEXT = 'days';
const EMPTY_TEXT = '-';
const BEST_OFFER_VALUE = 'Yes';

export const getDetailFields = (
  offer: TOffer,
  isSeller: Boolean,
): Array<TField> => {
  if (!offer || _isEmpty(offer)) return [];

  const funding = _get(offer, 'Funding.PurchaseType', '');

  const individualName = isSeller
    ? {
        label: 'Buyer Name',
        value: getBuyerName(offer),
      }
    : {
        label: 'Seller Name',
        value: getSellerName(offer),
      };
  const agentName = isSeller
    ? {
        label: 'Buyer Agent Name',
        value: getFullName(_get(offer, 'AgentInfo', {})),
      }
    : { label: 'NILE Agent Name', value: 'Khaled Yatim' };

  return [
    { label: 'Offer No', value: _get(offer, 'OfferNo', '') },
    individualName,
    agentName,
    {
      label: 'funding',
      value: funding,
    },
    {
      label: 'Loan Type',
      value: displayLoanType(offer),
    },
    {
      label: 'Down Payment',
      value: formatPrice(_get(offer, 'Funding.DownPayment', '')),
    },
    {
      label: 'Proof of funds',
      value:
        funding === FUNDING_PURCHASE_TYPES.cash
          ? formatPrice(_get(offer, 'Funding.CashProofOfFundsAmount', ''))
          : formatPrice(_get(offer, 'Funding.FinancingProofOfFundsAmount', '')),
    },
  ];
};

export const getFurnitureField = (offer: TOffer): TField => {
  return {
    label: 'Furniture',
    value: displayFurniture(
      _get(offer, 'Offer.Furniture', ''),
      _get(offer, 'Offer.FurnitureOther', ''),
    ),
  };
};

const displayOfferPrice = (counter: TCounter) => {
  const isBestOffer: Array<string> = _get(counter, 'IsBestOffer') || [];

  if (isBestOffer.includes(BEST_OFFER_VALUE)) return 'Best & Final';

  const offerPrice = _get(counter, 'OfferPrice');
  return displayPriceValue(offerPrice);
};

const displayDaysValue = (text: number | string | null) => {
  if (!text) return EMPTY_TEXT;

  const lowerCaseText = text.toString().toLowerCase();
  return lowerCaseText.includes(DAYS_TEXT)
    ? lowerCaseText
    : `${lowerCaseText} ${DAYS_TEXT}`;
};

const displayPriceValue = (price: number | null) =>
  price ? formatPrice(price) : EMPTY_TEXT;

const displayColHeader = (counter: TCounter) => {
  if (_isEmpty(counter)) {
    return 'Awaiting Counter Offer';
  }
  if (counter.counterType === COUNTER_TYPES.original) {
    return 'Original Offer';
  }
  return `${counter.counterType}\n${
    counter.SubmittedAt ? formatDate(counter.SubmittedAt) : ''
  }`;
};

const CONTINGENCY_TYPES_VALUES = Object.values(CONTINGENCY_TYPES);

const TABLE_DIMENSIONS: { cols: number, rows: TTableRow[] } = {
  cols: 7,
  rows: [
    { key: 'info', title: 'Information', displayFunction: displayColHeader },
    {
      key: 'info',
      title: 'Offer Price',
      displayFunction: displayOfferPrice,
    },
    {
      key: CONTINGENCY_TYPES.financeContingency,
      title: 'Financing',
    },
    {
      key: CONTINGENCY_TYPES.inspectionContingency,
      title: 'Inspection',
    },
    {
      key: CONTINGENCY_TYPES.appraisalContingency,
      title: 'Appraisal',
    },
    {
      key: CONTINGENCY_TYPES.saleOfHomeContingency,
      title: 'Home Sale',
    },
    {
      key: 'ClosingPeriod',
      title: 'Closing Period',
      displayFunction: displayDaysValue,
    },
    {
      key: 'RentBackDays',
      title: 'Rent Back Period',
      displayFunction: displayDaysValue,
    },
    {
      key: 'RentBackDailyAmount',
      title: 'Rent Back Amount per Day',
      displayFunction: displayPriceValue,
    },
  ],
};

const getOriginalCounter = (offer: TOffer): TCounter => {
  const originalOffer = _get(offer, 'Offer') || {};
  const fundingType = _get(offer, 'Funding.PurchaseType') || '';
  const counter = {
    counterType: COUNTER_TYPES.original,
    fundingType,
    ..._pick(originalOffer, [
      'OfferPrice',
      'FinanceContingency',
      'InspectionContingency',
      'AppraisalContingency',
      'FinanceContingencyDays',
      'InspectionContingencyDays',
      'AppraisalContingencyDays',
      'SaleOfHomeContingency',
      'ClosingPeriod',
      'RentBackDays',
      'RentBackDailyAmount',
    ]),
  };
  return counter;
};

const flatHistoryCounters = (offer: TOffer): Array<TCounter> => {
  const historyCounters = _get(offer, 'CounterOfferHistory') || [];
  const fundingType = _get(offer, 'Funding.PurchaseType') || '';

  return historyCounters.reduce(
    (result: Array<TCounter>, countersGroup: TCountersGroup) => {
      const buyerCounter = _get(countersGroup, 'BuyerCounter', {});
      if (!_isEmpty(buyerCounter))
        result.push({
          ...buyerCounter,
          counterType: COUNTER_TYPES.buyer,
          fundingType,
        });

      const agentCounter = _get(countersGroup, 'SellerCounter', {});
      if (!_isEmpty(agentCounter))
        result.push({
          ...agentCounter,
          counterType: COUNTER_TYPES.agent,
          fundingType,
        });

      return result;
    },
    [],
  );
};

const getCounters = (offer: TOffer): Array<TCounter> => {
  const originalCounter = getOriginalCounter(offer);
  const historyCounters = flatHistoryCounters(offer).slice(-5);

  return [originalCounter, ...historyCounters];
};

export const getHistoryRows = (offer: TOffer): Array<Array<string>> => {
  const counters = getCounters(offer);
  return TABLE_DIMENSIONS.rows.map(row => {
    const cells: Array<number | string> = [
      ...Array(TABLE_DIMENSIONS.cols).keys(),
    ];

    return cells.reduce((result, current, cellIndex) => {
      if (!cellIndex) {
        return [...result, row.title];
      }

      const counter = _get(counters, [cellIndex - 1]);
      const fieldKey = row.key;

      if (CONTINGENCY_TYPES_VALUES.includes(fieldKey))
        return [
          ...result,
          `${
            _isEmpty(counter)
              ? EMPTY_TEXT
              : displayContingency(counter, fieldKey, 'fundingType')
          }`,
        ];

      const displayData =
        row.key === 'info' ? counter : _get(counter, [fieldKey]);
      return [
        ...result,
        row.displayFunction ? row.displayFunction(displayData) : EMPTY_TEXT,
      ];
    }, []);
  });
};

export const getConfirmDialogMessage = (type: string): string =>
  `Are you sure? - This action cannot be reverted. Do you want to ${type} the offer?`;
